import { withScriptjs, withGoogleMap, GoogleMap, Marker } from "react-google-maps"

const Maps = withScriptjs(withGoogleMap((props) =>
  <GoogleMap
    defaultZoom={8}
    defaultCenter={{ lat: 34.755340, lng: -92.345230 }}
  >
    {props.isMarkerShown && <Marker position={{ lat: 34.755, lng: -92.345230 }} />}
  </GoogleMap>
))

export default Maps;